import { inject } from 'vue';
export const pageHeadTitleSymbol = Symbol(__DEV__ ? 'pageHeadTitle' : '');
export const usePageHeadTitle = () => {
    const pageHeadTitle = inject(pageHeadTitleSymbol);
    if (!pageHeadTitle) {
        throw new Error('usePageHeadTitle() is called without provider.');
    }
    return pageHeadTitle;
};
/**
 * Title to displayed in `<head>` tag
 */
export const resolvePageHeadTitle = (page, siteLocale) => `${page.title ? `${page.title} | ` : ``}${siteLocale.title}`;
