import { inject } from 'vue';
export const pageFrontmatterSymbol = Symbol(__DEV__ ? 'pageFrontmatter' : '');
export const usePageFrontmatter = () => {
    const pageFrontmatter = inject(pageFrontmatterSymbol);
    if (!pageFrontmatter) {
        throw new Error('usePageFrontmatter() is called without provider.');
    }
    return pageFrontmatter;
};
export const resolvePageFrontmatter = (pageData) => pageData.frontmatter;
